import { evntBus } from "./bus";
import Card from "./Card.vue";

export default {
  data: function () {
    return {
      data: "",
    };
  },
  components: {
    Card,
  },

  methods: {
    get_data() {
      const vm = this;
      frappe.call({
        method: "csf_tz.csf_tz.page.jobcards.jobcards.get_job_cards",
        args: {},
        async: true,
        callback: function (r) {
          if (r.message) {
            vm.data = r.message;
          }
        },
      });
    },
    open_card(item) {
      evntBus.$emit("open_card",item);
    },
  },
  created: function () {
    this.get_data();
  },
};
